export function getPremadeMap(numRows, numCols, mapType) {
    let fullMapGrid;
    switch(mapType) {
        case 1:
            fullMapGrid = getAllMap1NodeWeights();
            break;
        case 2:
            fullMapGrid = getAllMap2NodeWeights();
            break;
        case 3:
            fullMapGrid = getAllMap3NodeWeights();
            break;
        default:
    }
    const lastFullMapColIndex = fullMapGrid[0].length - 1;
    const selectedMapGrid = [];

    for(let r = 0; r < numRows; ++r) {
        selectedMapGrid.push([]);
        for(let c = 0; c < numCols; ++c) {
            selectedMapGrid[r][c] = fullMapGrid[r][c % lastFullMapColIndex];
        }
    }

    return selectedMapGrid;
}

